import React, {Component} from "react";
import Gif from "../Gif";
import API from "../../utils/API.js";
import Header from "../Header";
import Banner from "../Banner";
import Footer from "../Footer";


class GifContainer extends Component {

    state = {
        gifs: [],
        clicked: false,
        beenClicked: [],
        score: "duck",
        highscore: 0,
        ready: false
    };

    test = () => {
    console.log(this.state.gifs[0].id);
    }

    shuffle = (array) => {
        var currentIndex = array.length, temporaryValue, randomIndex;

        while (0 !== currentIndex) {
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;

            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }
        return array;
    };

    wasClicked = id => {
        console.log("clicked: "+ id);
        console.log("array before: " + this.state.beenClicked);
        console.log("found: " + this.state.beenClicked.find(function(element) {
            return element === id
        }
        ));
        if(this.state.beenClicked.find((element) => element === id) === undefined){
            this.state.beenClicked.push(id);
            this.shuffle(this.state.gifs);
            this.setState({score: this.state.score+=1});
            if(this.state.score > this.state.highscore){
                this.setState({highscore: this.state.highscore+=1});
            }
        }else{
            console.log("array: " + this.state.beenClicked);
            this.shuffle(this.state.gifs);
            this.setState({score: 0});
            this.setState({beenClicked: []});
        }
    };

    componentDidMount() {
        API.getGifs()
        .then(res => this.setState({ gifs: res.data.data , ready: true}));
        this.setState({score: 0});

    };

    render() {
        return(
            <div>
                <Header score={this.state.score} highscore={this.state.highscore}/>
                <Banner />
                {this.state.ready === true ? 
                     this.state.gifs.map(item => 
                        <Gif url={item.images.original.url} wasClicked={this.wasClicked} key={item.id} id={item.id}/>
                     )
                     : <p>Not ready</p>}
                <Footer />
            </div>
        );
    }
}

export default GifContainer;