import React from "react";
import "./style.css";

function Header(props) {
    return(
        <nav class="navbar fixed-top navbar-dark bg-dark">
            <ul>
                <li>Gif Clicky Game</li>
                <li>Click a gif to begin</li>
                <li>Score: {props.score} | Highscore: {props.highscore}</li>
            </ul>
        </nav>
    );
}

export default Header;